<template>
    <section>
        <Form ref="AddClient">
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
            <div id="accordion" class="col-12 pt-2">
                <div class="card mb-0">
                    <div class="card-header DefaultTab">
                        <a class="card-link collapsed" data-toggle="collapse" href="#collapseone">
                            <div class="conversation-date heading-border"><span>Advanced Settings</span></div>
                        </a>
                    </div>
                    <div id="collapseone" class="collapse show" data-parent="#accordion" style="">
                        <div class="card-body">
                            <dynamic-form lang="en" :buttons="buttons2" :schema="FormSchema2" v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </Form>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
export default{
   data(){
    return {
        UserType:0,
        FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'First Name',
                            name: "FirstName",
                            value: "",
                            placeholder: "Enter First Name",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: 'Last Name',
                            name: "LastName",
                            value: "",
                            placeholder: "Enter Last Name",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "TextField",
                            label: 'Email',
                            name: "Email",
                            value: "",
                            placeholder: "Enter Last Name",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Gender',
                            name: "Gender",
                            value: "",
                            config:{
                                options:[{value:'M',name:'Male'},{value:'F',name:'Female'}]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "RadioField",
                            label: 'Account',
                            name: "Gender",
                            value: 1,
                            config:{
                                options:[{value:1,name:'Existing'},{value:2,name:'Create New'}]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Parent Accounts',
                            name: "ParentAccounts",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Client Owner',
                            name: "ClientOwner",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        }
                    ],
                }
        ],
        FormSchema2: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: 'Time Zone',
                            name: "TimeZone",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Date Format',
                            name: "Date",
                            value: 1,
                            config:{
                                options:[{ name: "M/d/yyyy", value:"M/D/YYYY" },{ name:"dd/MM/yyyy", value:"DD/MM/YYYY" }]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Role',
                            name: "Role",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        }
                    ],
                }
        ],
        buttons2: [
            {
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
                onClick:this.SaveData
            }
        ],
    }
   },
   created:function(){
    var vm=this;
    setTimeout(() => {
        vm.GetParentAccount();
    }, 500);
    setTimeout(() => {
        vm.GetClientOwner();
    }, 500);
    setTimeout(() => {
        vm.GetTimeZone();
    }, 500);
    setTimeout(() => {
        vm.GetRole();
    }, 500);
    vm.UserType = vm.GetUserInfo.UserType;
   },
   methods:{
    GetParentAccount:async function(){
        var vm=this;
        var params=`moduleName=Ticketing&type=TALYGEN_ACCOUNT`
        await DataService.GetDDLData(params).then(function (response) {
            response.data.DATA.forEach(function (item,i) {
                vm.FormSchema[0].Data[5].config.options.push({
                    name:item.name,
                    value:item.value
                })
            })
        })
    },
    GetClientOwner:async function(){
        var vm=this;
        var params=`moduleName=Client&type=`
         DataService.GetClientOwner(params).then(function (response) {
            response.data.forEach(function (item,i) {
                vm.FormSchema[0].Data[6].config.options.push({
                    name:item.USERNAME,
                    value:item.USERID
                })
            })
        })
    },
    GetTimeZone:async function(){
        var vm=this;
        await DataService.GetTimeZone().then(function (response) {
            response.data.forEach(function (item,i) {
                vm.FormSchema2[0].Data[0].config.options.push({
                    name:item.display_name,
                    value:item.timezone_id 
                })
            });
        })
    },
    GetRole:async function(){
        var vm=this;
        var userTypeId=0;
        if(vm.UserType=="CA"){
            userTypeId=1;
        }
        else if(vm.UserType=="PM"){
            userTypeId=2;
        }
        else if(vm.UserType=="NU"){
            userTypeId=3;
        }
        else if(vm.UserType=="CL"){
            userTypeId=4;
        }
        var params=`moduleName=Ticketing&type=TALYGEN_ROLE&refId=${userTypeId}`
        await DataService.GetDDLData(params).then(function (response) {
            debugger
            response.data.DATA.forEach(function (item,i) {
                vm.FormSchema2[0].Data[2].config.options.push({
                    name:item.value,
                    value:item.name
                })
            })
        })
    },
    SaveData:function(){
        var vm=this;
        vm.$refs.AddClient.validate().then(result=>{
                if(result){

                }
            }) 
    }
   }
}
</script>
